<template>

  <div id="app">
   <h1 style="text-align:center; color:lightgreen;">VUE JS</h1>
   <div class="row">
        <div class="input-field col s12 ">
          <input placeholder="Press enter to post" id="first_name" type="text" class="validate" v-model="postName" @keyup.enter="addPost()" >
          <label for="first_name">First Name</label>
        </div>
 
  <div class="row">
    <div class="col s12 m12">
      <div class="card blue-grey darken-1">
        <div class="card-content white-text">
       <h1 > Comments</h1>
      <ul>
  <li v-for="post of posts" :key="post.id" id="post-list">
  <h5>{{post.body}} <a class="waves-effect waves-light btn"  v-on:click="deletePost(post.id)">Delete</a></h5>
  </li>
  </ul>
        </div>

      </div>
    </div>
  </div>
  </div>
  </div>
</template>

<script>
import axios from "axios";

const baseURL="https://my-json-server.typicode.com/typicode/demo/comments";

export default {
  name: 'App',
 data(){
   return{
     posts: [],
     postName:'',
        };
 },
 async created(){
try{
  const res=await axios.get(baseURL);
  this.posts=res.data;
 }catch(e){
   console.log(e);
 }
},
methods:{
 async addPost(){
  const res=await axios.post(baseURL,{body: this.postName});
  this.posts=[...this.posts,res.data];
  this.postName='';
  },
 async deletePost(id){
 axios.delete(`${baseURL}/${id}`)
  .then(function (response) {
    console.log(response)
  })
  .catch(function (error) {
    console.log(error)
  })
  this.posts=[this.posts];
  }
}
}
</script>

<style>
#app {

}
</style>
